*, :before, :after {
  box-sizing: border-box;
}

ul[class], ol[class] {
  padding: 0;
}

body, h1, h2, h3, h4, p, ul[class], ol[class], li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizespeed;
  min-height: 100vh;
  line-height: 1.5;
}

ul[class], ol[class] {
  list-style: none;
}

a:not([class]) {
  -webkit-text-decoration-skip-ink: auto;
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

article > * + * {
  margin-top: 1em;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

:root {
  --pink: #dc56c5;
  --black: #0e0e0e;
  --gray: #939393;
  --another-gray: #4b4b4b;
  --violet: #dc56c54d;
  --another-violet: #200d1d99;
  --blue: #4c00fe;
}

body {
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  display: none;
}

.loader {
  aspect-ratio: 1;
  z-index: 100;
  background-color: #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 250vw;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)rotate(0);
}

.loader.active {
  animation: 3s ease-in scaling;
}

.loader img {
  aspect-ratio: 135 / 65;
  width: 20vw;
}

.header {
  background-image: url("header_bg.09be584f.png");
  background-position: center;
  background-size: cover;
  padding-top: 40px;
  padding-bottom: 180px;
}

.header__logo {
  position: absolute;
  top: 0;
  left: 0;
}

.header__title {
  text-align: center;
  color: var(--pink);
  max-width: 588px;
  margin: auto;
  font-family: Montserrat;
  font-size: 50px;
  font-weight: 900;
  line-height: 60px;
}

.header__wrapper {
  justify-content: center;
  gap: 30px;
  margin-top: 50px;
  display: flex;
}

.header__input {
  backdrop-filter: blur(20px);
  color: var(--gray);
  background: #fff3;
  border: none;
  border-radius: 20px;
  width: 270px;
  padding: 10px 30px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
}

.header__input::placeholder {
  color: var(--gray);
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
}

.header__input:focus {
  border: none;
  outline: none;
}

.main {
  background-color: var(--black);
}

.container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 70px 65px;
  max-width: 930px;
  margin: auto;
  display: flex;
}

.main__card {
  text-align: center;
  animation: move1 var(--time) ease;
  flex-direction: column;
  gap: 5px;
  width: 180px;
  display: flex;
  position: relative;
}

.main__card-preview {
  aspect-ratio: 9 / 16;
  object-fit: cover;
  border-radius: 50px 0;
  width: 100%;
}

.main__card:after {
  content: "";
  border: 1px solid var(--violet);
  border-radius: 50px 0;
  width: 150px;
  height: 150px;
  display: block;
  position: absolute;
  top: -15px;
  right: -15px;
}

.main__card-title {
  color: var(--pink);
  cursor: pointer;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 700;
}

.main__card-data {
  color: #fff;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
}

.main__card-place {
  color: #fff;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
}

.wrapper__modal {
  backdrop-filter: blur(3px);
  background: var(--another-violet);
  justify-content: center;
  align-items: self-start;
  width: 100%;
  height: 100vh;
  padding: 200px 0;
  animation: .4s linear appearance;
  display: none;
  position: fixed;
  top: 0;
  overflow-y: scroll;
}

.wrapper__modal::-webkit-scrollbar {
  display: none;
}

.modal {
  background: #fff;
  border-radius: 300px 0;
  padding: 60px 30px;
  animation: .5s ease-in popping;
  display: none;
  position: relative;
}

.modal__icon {
  aspect-ratio: 1;
  object-fit: cover;
  border-radius: 50%;
  width: 132px;
  margin: auto;
}

.modal__close {
  cursor: pointer;
  position: absolute;
  top: 30px;
  right: 30px;
}

.modal__author {
  cursor: pointer;
  color: #4c00fe;
  box-sizing: border-box;
  border: 1px solid #4c00fe;
  border-radius: 5px;
  width: max-content;
  margin: 40px auto 0;
  padding: 10px 16px;
  font-family: Montserrat;
  display: block;
}

.modal__content {
  gap: 60px;
  margin-top: 40px;
  display: flex;
}

.modal__preview {
  aspect-ratio: 9 / 16;
  object-fit: cover;
  width: 360px;
}

.modal__title {
  color: var(--pink);
  font-family: Montserrat;
  font-size: 24px;
  font-weight: 600;
  line-height: 29px;
}

.modal__text {
  color: var(--black);
  padding-bottom: 20px;
  font-family: Montserrat;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
}

.modal__btn {
  background: var(--blue);
  color: #fff;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 10px 20px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 500;
}

.footer {
  background-color: var(--black);
  padding-top: 65px;
  padding-bottom: 20px;
}

.footer__numbers {
  justify-content: center;
  gap: 5px;
  display: flex;
}

.footer__numbers-number {
  color: #fff;
  cursor: pointer;
  border: 1px solid #0000;
  border-radius: 10px 0;
  padding: 5px 10px;
  font-family: Montserrat;
  transition: all .3s;
}

.footer__numbers-number:hover, .footer__numbers-number.active {
  color: var(--pink);
  border: 1px solid var(--pink);
}

.footer__text {
  color: var(--gray);
  text-align: center;
  margin-top: 80px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
}

@keyframes scaling {
  0% {
    width: 250vw;
  }

  50% {
    width: 40vw;
    transform: translate(-50%, -50%)rotate(0);
  }

  100% {
    width: 40vw;
    transform: translate(-300%, -50%)rotate(-360deg);
  }
}

@keyframes appearance {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes popping {
  0% {
    scale: 0;
  }

  50% {
    scale: 1;
  }

  60% {
    scale: 1.3;
  }

  70% {
    scale: 1;
  }
}

@keyframes move1 {
  0% {
    opacity: 0;
    left: var(--from);
  }

  100% {
    opacity: 1;
    left: 0;
  }
}

@media (width <= 800px) {
  .header {
    background-image: url("image.ac10877e.png");
  }

  .header__title {
    max-width: 353px;
    font-size: 30px;
    line-height: 37px;
  }

  .header__logo {
    width: 151px;
    height: 140px;
  }

  .container {
    gap: 60px 30px;
  }

  .modal__preview {
    width: 230px;
  }

  .modal__text {
    font-size: 16px;
  }

  .modal__title {
    font-size: 20px;
  }

  .modal__icon {
    width: 80px;
  }
}

@media (width <= 584px) {
  .header {
    padding-top: 70px;
    padding-bottom: 60px;
  }

  .header__title {
    max-width: 212px;
    font-size: 18px;
  }

  .header__logo {
    width: 110px;
    height: 90px;
  }

  .header__wrapper {
    flex-direction: column;
    align-items: center;
    gap: 25px;
  }

  .main__card {
    width: 128px;
  }

  .main__card-title, .main__card-data {
    font-size: 14px;
  }

  .main__card-place {
    font-size: 12px;
  }

  .footer__text {
    padding: 0 34px;
  }

  .modal__preview {
    display: none;
  }

  .modal__content {
    text-align: center;
    justify-content: center;
  }

  .modal {
    border-radius: 150px 0;
    padding: 60px 12px;
  }

  .modal__author {
    margin: 40px auto 70px;
  }

  .main__card:after {
    width: 105px;
    height: 105px;
  }
}
